<template>
    <views class="faq-item">
        <views class="faq-q">
            <views class="icon">
                <images :src="qIcon"></images>
            </views>
            <views class="title">{{ item.question }}</views>
        </views>
        <views class="faq-a">
            <views class="icon">
                <images :src="aIcon"></images>
            </views>
            <views class="title">{{ item.answer }}</views>
        </views>
    </views>
</template>

<script>
export default {
    props: {
        item: {
            type: Object,
            default: () => ({})
        }
    },
    data() {
        return {
            aIcon: require('@/assets/images/a.png'),
            qIcon: require('@/assets/images/q.png')
        }
    }
}
</script>

<style lang="scss" scoped>
@import './index.scss';
</style>
